export interface User {
  id: number
  email: string
  name: string
  pictureUrl?: string
}

export interface ReadingDay {
  day: number
  startDate: string
  endDate: string
  bookName: string
}

export interface Book {
  title: string
  coverUrl: string
  rating: number
}

export async function loginUser(email: string, name?: string, pictureUrl?: string): Promise<User> {
  const response = await fetch("/api/auth/login", {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify({ email, name, pictureUrl }),
  })

  if (!response.ok) {
    throw new Error("Erro ao fazer login")
  }

  const data = await response.json()
  return data.user
}

export async function getReadingData(email: string, year: number, month: number) {
  const response = await fetch(`/api/reading-data?email=${encodeURIComponent(email)}&year=${year}&month=${month}&t=${Date.now()}`, {
    cache: 'no-store'
  })

  if (!response.ok) {
    throw new Error("Erro ao buscar dados de leitura")
  }

  const data = await response.json()
  return data.data
}

export async function saveReadingDay(
  email: string,
  year: number,
  month: number,
  day: number,
  startDate: string,
  endDate: string,
  bookName: string,
  action: "START_READING" | "FINISH_READING"
) {
  const response = await fetch("/api/reading-data", {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify({ email, year, month, day, startDate, endDate, bookName, action }),
  })

  if (!response.ok) {
    throw new Error("Erro ao salvar dados")
  }

  return response.json()
}

export async function getBookReviews(email: string, year: number, month: number) {
  const response = await fetch(`/api/book-reviews?email=${encodeURIComponent(email)}&year=${year}&month=${month}`)

  if (!response.ok) {
    throw new Error("Erro ao buscar livros")
  }

  const data = await response.json()
  return data.data
}

export async function saveBookReviews(email: string, year: number, month: number, books: Book[]) {
  const response = await fetch("/api/book-reviews", {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify({ email, year, month, books }),
  })

  if (!response.ok) {
    throw new Error("Erro ao salvar livros")
  }

  return response.json()
}