"use client"

import { useState, useEffect, useCallback, useMemo } from "react"
import { useSession, signOut } from "next-auth/react"
import { MonthCalendar } from "@/components/month-calendar"
import { MonthReview } from "@/components/month-review"
import { LoginScreen } from "@/components/login-screen"
import { Button } from "@/components/ui/button"
import { ChevronLeft, ChevronRight, BookOpen, LogOut } from "lucide-react"
import { saveCurrentUser, getCurrentUser, clearCurrentUser, type UserData } from "@/lib/storage"

export default function Home() {
  const { data: session, status } = useSession()
  const [user, setUser] = useState<UserData | null>(null)
  const [isLoading, setIsLoading] = useState(true)
  const [currentYear, setCurrentYear] = useState(() => new Date().getFullYear())
  const [currentMonth, setCurrentMonth] = useState(() => new Date().getMonth())
  const [showBack, setShowBack] = useState(false)

  const months = useMemo(() => {
    const isLeapYear = (currentYear % 4 === 0 && currentYear % 100 !== 0) || currentYear % 400 === 0
    return [
      { name: "Janeiro", days: 31 },
      { name: "Fevereiro", days: isLeapYear ? 29 : 28 },
      { name: "Março", days: 31 },
      { name: "Abril", days: 30 },
      { name: "Maio", days: 31 },
      { name: "Junho", days: 30 },
      { name: "Julho", days: 31 },
      { name: "Agosto", days: 31 },
      { name: "Setembro", days: 30 },
      { name: "Outubro", days: 31 },
      { name: "Novembro", days: 30 },
      { name: "Dezembro", days: 31 },
    ]
  }, [currentYear])

  useEffect(() => {
    if (status === "loading") return
    
    const localUser = getCurrentUser()
    if (session?.user) {
      const userData: UserData = {
        name: session.user.name || "Usuário",
        email: session.user.email || "",
        provider: "google"
      }
      setUser(userData)
      saveCurrentUser(userData)
    } else if (localUser) {
      setUser(localUser)
    }
    setIsLoading(false)
  }, [session, status])

  const handleLogout = useCallback(() => {
    clearCurrentUser()
    setUser(null)
    signOut({ callbackUrl: "/" })
  }, [])

  const handlePrevMonth = () => {
    if (currentMonth === 0) {
      setCurrentYear(currentYear - 1)
      setCurrentMonth(11)
    } else {
      setCurrentMonth(currentMonth - 1)
    }
    setShowBack(false)
  }

  const handleNextMonth = () => {
    if (currentMonth === 11) {
      setCurrentYear(currentYear + 1)
      setCurrentMonth(0)
    } else {
      setCurrentMonth(currentMonth + 1)
    }
    setShowBack(false)
  }

  if (isLoading) {
    return (
      <div className="flex min-h-screen items-center justify-center bg-background">
        <BookOpen className="h-8 w-8 animate-pulse text-primary" />
      </div>
    )
  }

  if (!user) return <LoginScreen onLogin={(u) => { saveCurrentUser(u); setUser(u); }} />

  return (
    <main className="min-h-screen bg-background p-4 md:p-8">
      <div className="mx-auto max-w-6xl">
        <div className="mb-8 flex items-center justify-between">
          <div className="flex items-center gap-3">
            <div className="rounded-lg bg-primary/10 p-2">
              <BookOpen className="h-6 w-6 text-primary" />
            </div>
            <div>
              <h1 className="font-serif text-2xl font-bold text-foreground md:text-3xl">
                Literário {currentYear}
              </h1>
              <p className="text-sm text-muted-foreground">Olá, {user.name}</p>
            </div>
          </div>

          <div className="flex items-center gap-2">
            <Button variant="outline" size="icon" onClick={handleLogout} className="h-9 w-9 bg-transparent"><LogOut className="h-4 w-4" /></Button>
            <Button variant="outline" size="icon" onClick={handlePrevMonth} className="h-9 w-9 bg-transparent"><ChevronLeft className="h-4 w-4" /></Button>
            <Button variant="outline" size="icon" onClick={handleNextMonth} className="h-9 w-9 bg-transparent"><ChevronRight className="h-4 w-4" /></Button>
          </div>
        </div>

        <div className="mt-6 flex justify-center mb-8">
          <Button onClick={() => setShowBack(!showBack)} variant="secondary" className="gap-2 px-6">
            {showBack ? "← Ver Calendário" : "Ver Livros Lidos →"}
          </Button>
        </div>

        <div className="relative">
           {!showBack ? (
              <MonthCalendar
                month={months[currentMonth].name}
                days={months[currentMonth].days}
                year={currentYear}
                userEmail={user.email}
                monthIndex={currentMonth}
              />
           ) : (
              <MonthReview 
                month={months[currentMonth].name} 
                userEmail={user.email} 
                monthIndex={currentMonth} 
              />
           )}
        </div>
      </div>
    </main>
  )
}