"use client"

import { useState, useEffect } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { BookOpen, Loader2, AlertCircle } from "lucide-react"
import { getReadingData, saveReadingDay } from "@/lib/api-client"

export function MonthCalendar({ month, days, year, userEmail, monthIndex }: any) {
  const [readings, setReadings] = useState<any[]>([])
  const [tempBookNames, setTempBookNames] = useState<Record<number, string>>({})
  const [isUpdating, setIsUpdating] = useState(false)
  const [error, setError] = useState(false)

  async function loadData() {
    if (!userEmail) return
    try {
      const data = await getReadingData(userEmail, year, monthIndex + 1)
      setReadings(Array.isArray(data) ? data : [])
      setError(false)
    } catch (err) {
      console.error("Erro ao carregar dados:", err)
      setError(true)
    }
  }

  useEffect(() => {
    loadData()
  }, [userEmail, monthIndex, year])

  async function handleAction(day: number, action: "START_READING" | "FINISH_READING", bookName?: string) {
    if (isUpdating || !userEmail) return
    const name = bookName || tempBookNames[day]
    if (!name) return

    setIsUpdating(true)
    // Usamos o meio-dia (12:00) para garantir que a data não mude de dia por causa do fuso horário
    const date = new Date(year, monthIndex, day, 12, 0, 0).toISOString()

    try {
      await saveReadingDay(
        userEmail, year, monthIndex + 1, day,
        action === "START_READING" ? date : "",
        action === "FINISH_READING" ? date : "",
        name, action
      )
      setTempBookNames(p => ({ ...p, [day]: "" }))
      await loadData()
    } catch (err) {
      alert("Erro ao salvar no banco. Verifique sua conexão.")
    } finally {
      setIsUpdating(false)
    }
  }

  return (
    <div className="space-y-4">
      {error && (
        <div className="bg-destructive/15 p-4 rounded-lg flex items-center gap-2 text-destructive text-sm font-medium">
          <AlertCircle size={16} /> Erro de conexão com o banco de dados.
        </div>
      )}
      
      <div className="grid grid-cols-2 gap-3 md:grid-cols-7 md:gap-4">
        {Array.from({ length: days || 30 }, (_, i) => i + 1).map((day) => {
          // FILTRO ULTRA-SEGURO:
          const dayReadings = readings.filter((r) => {
            if (!r.start_date) return false
            
            const start = new Date(r.start_date)
            const end = r.end_date ? new Date(r.end_date) : null
            const current = new Date(year, monthIndex, day, 12, 0, 0)

            // Só mostra se começou hoje ou antes, e se ainda não terminou
            const started = start <= current
            const finished = end && end < current
            
            return started && !finished
          })

          return (
            <div key={`${monthIndex}-${day}`} className="min-h-[160px] rounded-lg border p-2 flex flex-col bg-card shadow-sm transition-all hover:ring-2 hover:ring-primary/20">
              <span className="font-bold text-primary mb-2 text-sm">{day}</span>
              
              <div className="flex-1 space-y-1 overflow-y-auto">
                {dayReadings.map((r, idx) => (
                  <div key={idx} className="p-1 rounded text-[10px] border bg-primary/5 border-primary/20">
                    <p className="truncate font-bold text-foreground">{r.book_name.toUpperCase()}</p>
                    {r.status === "lendo" && (
                      <Button 
                        size="sm" className="h-5 w-full text-[8px] mt-1 font-bold" variant="destructive"
                        onClick={() => handleAction(day, "FINISH_READING", r.book_name)}
                        disabled={isUpdating}
                      >
                        ENCERRAR
                      </Button>
                    )}
                  </div>
                ))}
              </div>

              <div className="mt-2 pt-2 border-t border-border/50">
                <Input 
                  className="h-7 text-[10px] bg-background mb-1" placeholder="Novo livro..."
                  value={tempBookNames[day] || ""}
                  onChange={e => setTempBookNames(p => ({ ...p, [day]: e.target.value }))}
                />
                <Button 
                  className="h-7 w-full text-[10px] font-bold" 
                  onClick={() => handleAction(day, "START_READING")}
                  disabled={isUpdating || !tempBookNames[day]}
                >
                  {isUpdating ? <Loader2 className="h-3 w-3 animate-spin mx-auto" /> : "INICIAR"}
                </Button>
              </div>
            </div>
          )
        })}
      </div>
    </div>
  )
}